import {
	mapState
} from 'vuex';
export default {
	components: {},
	data() {
		return {
			salesman_dataList: [],
			listTouchStart: 0,
			listTouchDirection: null,
			modalName: null,
		}
	},
	computed: {
		...mapState(['token'])
	},
	methods: {
		 getSalesManData() {
			var now_token = this.token
			uni.request({
				url: this.global_baseUrl + '/salesman/',
				header: {
					'Authorization': ' Token ' + now_token
				},
				method: "GET",
				success: (res) => {
					this.check_statusCode(res.statusCode);
					this.salesman_dataList = res.data[0].salesman_formal;
				}
			});
		},
		ViewImage(e) {
			var imgList=[];
			imgList.push(e)
			uni.previewImage({
				urls: imgList,
			});
		},
	},
	onLoad() {
		uni.showLoading({
			title:'数据加载中'
		})
		this.getSalesManData();
		setTimeout(() => {
			uni.hideLoading();
		}, 1000)
	},


}
